import React from "react";
import Skeleton from "@material-ui/lab/Skeleton";

const PrePosts = () => {
    const size = window.innerWidth;

    return (
        <>
            <div className="posts-loading-wrapper">
                <div>
                    <Skeleton
                        className="mb"
                        animation="wave"
                        variant="circle"
                        width={size / 11}
                        height={size / 11}
                    />
                    <Skeleton
                        className="mb"
                        animation="wave"
                        variant="text"
                        width={size / 1.75}
                        height={size / 17}
                    />
                </div>
                <Skeleton
                    animation="wave"
                    variant="rect"
                    width={size / 1.5}
                    height={size / 11}
                    className="mb"
                />
                <Skeleton
                    animation="wave"
                    variant="rect"
                    width={size / 1.5}
                    height={size / 17}
                />
            </div>
        </>
    );
};

export default PrePosts;
