import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import PersonIcon from "@material-ui/icons/Person";
import RoomIcon from "@material-ui/icons/Room";
import EmailIcon from "@material-ui/icons/Email";
import CakeIcon from "@material-ui/icons/Cake";
import PhoneIphoneIcon from "@material-ui/icons/PhoneIphone";
import PermContactCalendarIcon from "@material-ui/icons/PermContactCalendar";
import "./profile.css";
import PropTypes from "prop-types";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`nav-tabpanel-${index}`}
            aria-labelledby={`nav-tab-${index}`}
            {...other}
            className="tabpanel"
        >
            {value === index && (
                <Box p={3}>
                    <>{children}</>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

const useStyles = makeStyles({
    root: {
        flexGrow: 1,
        width: "100%",
    },
});
const Profile = (props) => {
    const { data } = props;
    const classes = useStyles();
    const [value, setValue] = useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    return (
        <>
            <Grid container spacing={0}>
                <div className="profile-container">
                    <div className="profile-pic">
                        <img src={data.picture.large} alt="" />
                    </div>
                    <Paper square className={classes.root}>
                        <TabPanel value={value} index={0}>
                            <div className="info-wrapper">
                                <h2>Hey There, Im </h2>
                                <div>
                                    <span>{data.name.first} </span>
                                    <span>{data.name.last}</span>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value={value} index={1}>
                            <div className="info-wrapper">
                                <h2>My Address is </h2>
                                <div>
                                    <span>{data.location.city} - </span>
                                    <span>{data.location.state} - </span>
                                    <span>
                                        {data.location.timezone.description} -{" "}
                                    </span>
                                    <span>{data.location.postcode}</span>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value={value} index={2}>
                            <div className="info-wrapper">
                                <h2>My Email Address is</h2>
                                <div>
                                    <span>{data.email}</span>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value={value} index={3}>
                            <div className="info-wrapper">
                                <div>
                                    <h2>
                                        Im <span>{data.dob.age} </span>
                                        Years old
                                    </h2>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value={value} index={4}>
                            <div className="info-wrapper">
                                <h2>Contact Me On</h2>
                                <div className="flex-col">
                                    <span>Phone: {data.phone} </span>
                                    <br />
                                    <span>Cell: {data.cell}</span>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value={value} index={5}>
                            <div className="info-wrapper">
                                <h2>Account Info</h2>
                                <div className="flex-col">
                                    <span>Username: {data.login.username}</span>
                                    <br />
                                    <span>Password: {data.login.password}</span>
                                    <span></span>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value={value} index={6}>
                            <div className="info-wrapper">
                                <span></span>
                                <span></span>
                                <span></span>
                            </div>
                        </TabPanel>

                        <Tabs
                            value={value}
                            onChange={handleChange}
                            variant="scrollable"
                            indicatorColor="primary"
                            textColor="primary"
                            aria-label="icon tabs example"
                            scrollButtons="desktop"
                            orientation="horizontal"
                        >
                            <Tab icon={<PersonIcon />} aria-label="profile" />
                            <Tab icon={<RoomIcon />} aria-label="sth" />
                            <Tab icon={<EmailIcon />} aria-label="person" />
                            <Tab icon={<CakeIcon />} aria-label="shit" />
                            <Tab
                                icon={<PhoneIphoneIcon />}
                                aria-label="person"
                            />
                            <Tab
                                icon={<PermContactCalendarIcon />}
                                aria-label="person"
                            />
                        </Tabs>
                    </Paper>
                </div>
            </Grid>
        </>
    );
};

export default Profile;
